import { useState, useEffect } from "react";

let useProgress = (animate, time) => {
  let [progress, setProgress] = useState(0);

  useEffect(
    () => {
      if (animate) {
        let rafId = null;
        let start = null;
        let step = timestamp => {
          if (!start) start = timestamp;
          let progress = timestamp - start;
          setProgress(progress);
          if (progress < time) {
            rafId = requestAnimationFrame(step);
          }
        };
        rafId = requestAnimationFrame(step);
        return () => cancelAnimationFrame(rafId);
      }
    },
    [animate, time]
  );

  return animate ? Math.min(progress / time, time) : 0;
}

export default function Button () {
  let progress = useProgress(true,1000);
  console.log(Math.floor((progress*100)-1))

  return (
    <div className='button-container' style={{ opacity: `${progress * 100}%`}}>
      <div className='button' style={{ marginLeft: `${progress * 48}%` }}>
        <div className='circle'>
          <h2 className='down-arrow'>&#709;</h2>
        </div>
      </div>
      <style jsx global>{`
        .button-container {
          z-index: 10;
          position: relative;
          margin-top: -55px;
          cursor: pointer;
        }
        .remove-underline {
          text-decoration: none;
        }
        .button {
          margin-left: 100%;
        }
        .circle {
          background: rgba(255,255,255,1);
          width: 50px;
          height: 50px;
          border-radius: 50%;
          text-align: center;
        }
        .arrow {
          padding-top: 12px;
          color: rgba(20,20,20,1);
          font-size: 36px;
          transform:scale(1.25,1);
          font-family: helveitica;
        }
      `}</style>
    </div>
  )
}
